<template>
	<view class="check-tiku-all-wrap">
		<view class="tiku-title">*同类别下题库可同时选择</view>
			
		<!--题库分类-->
		<view class="fenlei-wrap">
			
			<view class="fenlei-item" v-for="(item, index) in tiku_classi" :key="index">
				<view class="fenlei-name">{{item.name}}</view>
				<!-- 分类下属题库名称 -->
				<block v-for="(it,idx) in item.list" :key="idx">
					<view class="tiku-item">
						<text class="tiku-name">{{it.name}}</text>
						<text @click="checkticlassi(it.id)" class="duihao-txt" :class="{checked : (checktikuids.indexOf(it.id) != '-1')}"></text>
					</view>
				</block>
			</view>
					
			
		</view>
		
		<!-- 开始答题 -->
		<view @click="beginQuestion" class="zuoti-btn" type="default">开始做题</view>
		
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
				tiku_classi: {}, //题库类别数组
				checktikuids: [], //选的题库id
			}
		},
		onReady() {
			// http://edu-layui.sdwanyue.com/api/?s=App.Topic.GetClassList
			uni.request({
				url: 'http://edu-layui.sdwanyue.com/api/?s=App.Topic.GetClassList',
				method: 'GET',
				data: {
				},
				success: res => {
					console.log(res);
					if(res.data.data.code != '0') {
						return;
					}
					this.tiku_classi =  res.data.data.info; 
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				},
			});
		},
		methods: {
			// 选择分类题库
			checkticlassi(tiku_id){
				console.log(tiku_id);
				if(this.checktikuids.indexOf(tiku_id) != '-1') {
					let index = this.checktikuids.indexOf(tiku_id);
					this.checktikuids.splice(index,1);
				} else {
					this.checktikuids.push(tiku_id);
				}
				
			},
			// 开始做题
			beginQuestion() {
				uni.navigateTo({
					url: '../shuati-info/shuati-info?tiku_ids=' + JSON.stringify(this.checktikuids),
				});
			}
		}
	}
</script>

<style>
	
	.check-tiku-all-wrap {
		width: 90%;
		margin: 0 auto;
		padding-top: 20rpx;
	}
	
	.fenlei-wrap {
		margin-bottom: 150rpx;
	}
	
	.tiku-title {
		color: #B3B3B3;
		font-size: 22rpx;
	}
	
	.fenlei-item {
		padding-top: 25rpx;
	}
	
	.fenlei-name {
		font-size: 30rpx;
		font-weight: bold;
		color: #323232;
		margin-bottom: 15rpx;
	}
	
	.tiku-item{
		height: 102rpx;
		display: flex;
		align-items: center;
	}
	
	.tiku-item .tiku-name {
	   display: inline-block;
	   width: 90%;
	   font-size: 26rpx;
	   white-space: nowrap;
	   overflow: hidden;
	   text-overflow: ellipsis;
	}
	
	.duihao-img {
		width: 32rpx;
		height: 32rpx;
	}
	
	.duihao-txt {
		display: inline-block;
		width: 32rpx;
		height: 32rpx;
		border: 2rpx solid #DCDCDC;
		border-radius: 50%;
	
	}
	
	.checked {
		background: url(../../static/images/duihao11.png) no-repeat center;
		background-size: 100%;
		border: 2rpx solid #2adba5;
	}
	
	.zuoti-btn {
		width: 90%;
		height: 80rpx;
		line-height: 80rpx;
		border-radius: 10rpx;
		text-align: center;
		position: fixed;
		bottom: 20rpx;
	    left: 50%;
	    -webkit-transform: translateX(-50%);
		transform: translateX(-50%);
		background-color: #38DAA6;
		color: #FFFFFF;
		font-size: 30rpx;
		z-index: 999;
	}
	
	
</style>
